"use client";

import { useState, useEffect } from "react";

export default function RecommendationsPage() {
  const [user, setUser] = useState(null);
  const [recommendations, setRecommendations] = useState([]);

  useEffect(() => {
    fetch("/api/auth/me")
      .then((res) => res.json())
      .then((data) => {
        if (data.user) {
          setUser(data.user);
          // Load user-specific recommendations
          fetch(`/api/recommendations?userId=${data.user.id}`)
            .then(res => res.json())
            .then(data => setRecommendations(data));
        }
      });
  }, []);

  if (!user) return <p>Please log in to see recommendations.</p>;

  return (
    <div className="recommendations-page">
      <h2>Recommendations</h2>
      <ul>
        {recommendations.map((rec, idx) => (
          <li key={idx}>{rec.text}</li>
        ))}
      </ul>
      {user.experienceLevel === "beginner" && <p>Extra beginner tips: Start with simple actions like recycling or reducing plastic!</p>}
    </div>
  );
}
